import React from 'react';
import Container from 'react-bootstrap/Container';
//import Row from 'react-bootstrap/Row';
//import Col from 'react-bootstrap/Col';

import Button from 'react-bootstrap/Button';
//import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import { gsap } from "gsap";
import { Transition } from "react-transition-group";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
    this.toggleComponent = this.toggleComponent.bind(this);
  }

  toggleComponent() {
    this.setState({ show: !this.state.show });
  }

  render () {
    const { show } = this.state;

    const containerStyle = {
      backgroundColor: 'white',
      minHeight: '30vh',
      maxHeight: '50vh',
      minWidth: '30vw',
      maxWidth: '50vw',
      alignItems: 'flex-start',
      display: 'flex',
      justifyContent: 'space-around',
      flexDirection: 'row',
      flexWrap: 'wrap',
      borderColor: 'blue',
      borderRadius: 20,
      padding: '16px',
    };

    const buttonStyle = {
      margin: '8px',
      fontWeight: 'bold',
    };

    // Can get rid of style: App-logo, App-link
    // Also remove the animtion styles
    return (
      <div ref={this.wrapper}>
      <div className="App">
        <header className="App-header">
          <img src={require("./cake.png")} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
          <p>
            Click a Button
          </p>
          <Container style={containerStyle}>
            <Button style={buttonStyle} onClick={this.toggleComponent} variant="outline-info">Toggle</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            <Button style={buttonStyle} variant="outline-info">Info</Button>
            [
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>,
              <Button style={buttonStyle} variant="outline-info">Info</Button>
            ]

            <Button style={{position: 'absolute', right: '0px', bottom: '0px'}} variant="outline-info">Info</Button>
            <Button style={{position: 'absolute', right: '0px', top: '0px'}} variant="outline-info">Info</Button>
            <Button style={{position: 'absolute', left: '0px', bottom: '0px'}} variant="outline-info">Info</Button>
            <Button style={{position: 'absolute', left: '0px', top: '0px'}} variant="outline-info">Info</Button>
            <Button style={{position: 'absolute', left: '50%', top: '0px', transform: 'translate(-50%, 0%)', }} variant="outline-info">center</Button>

          </Container>
        </header>
      </div>
        <Transition
          timeout={1000}
          mountOnEnter
          unmountOnExit
          in={show}
          addEndListener={(node, done) => {
            gsap.to(node, 0.5, {
              x: show ? 0 : 100,
              autoAlpha: show ? 1 : 0,
              onComplete: done
            });
          }}
        >
          <div className="card col-6 transition-card simple" style={{position: 'absolute', left: '0%', top: '50%', transform: 'translate(0%, -50%)', }}>
            <div className="card-body">
              <h5 className="card-title">
                Animating React with GSAP
                <button
                  type="button"
                  className="close"
                  onClick={() => this.setState({ show: false })}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </h5>
              <p className="card-text">This entire element is mounted before the in animation starts and unmounted after the out animation is complete!!</p>
            </div>
          </div>
        </Transition>
      </div>
    );
  }
}

export default App;
